import React from "react";
import {Link, useNavigate} from "react-router-dom";
import {TestImage} from "../testComponents/TestImage";
import {useDispatch} from "react-redux";
import {logout} from "../authSlice";
export function LoggedInUser({user})
{
    const dispatch = useDispatch();
    const navigate = useNavigate()
    const f1 = (e) => {

        dispatch(logout())
        navigate('/login')
    }

    return (
        <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
            <li className="nav-item dropdown">
                <a className="nav-link dropdown-toggle" data-bs-toggle="dropdown" href="#" role="button"
                   aria-expanded="false">{user.name}</a>
                <ul className="dropdown-menu">
                    <li><a className="dropdown-item" href="#"><img  width="50" height="50" src={`data:image/gif;base64,${user.image}`}/></a></li>
                    <li><a className="dropdown-item" href="#">{user.email}</a></li>
                    <li><a className="dropdown-item"  onClick={f1}>Logout</a></li>
                    <li><hr className="dropdown-divider"/></li>
                    <li><a className="dropdown-item" href="#">Separated link</a></li>
                </ul>
            </li>

        </ul>
    )
}