import React, { Component } from 'react'
import { useForm } from "react-hook-form";
import { useSelector, useDispatch } from 'react-redux'
import {getTokenAsync, saveToken} from '../authSlice'
import {useNavigate} from "react-router-dom";
export default function Login(props) {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const authenticated = useSelector(state=>(state.auth.token != null))
    const expires_in = useSelector(state=>state.auth.expires_in)
    const token = useSelector(state=>state.auth.token)
    const { register, handleSubmit, watch, formState: { errors } } = useForm();
    const onSubmit = data => {
        dispatch(getTokenAsync(data));
       navigate(props.afterUrl)

    }

        return (

            <form onSubmit={handleSubmit(onSubmit)}>
                <h3>Sign In</h3>
                <div className="mb-3">
                    <label>Email address</label>
                    <input
                        type="email"
                        className="form-control"
                        placeholder="Enter email"
                        {...register("email")}
                    />
                </div>
                <div className="mb-3">
                    <label>Password</label>
                    <input
                        type="password"
                        className="form-control"
                        placeholder="Enter password"
                        {...register("password")}
                    />
                </div>
                <div className="mb-3">
                    <div className="custom-control custom-checkbox">
                        <input
                            type="checkbox"
                            className="custom-control-input"
                            id="customCheck1"
                        />
                        <label className="custom-control-label" htmlFor="customCheck1">
                            Remember me
                        </label>
                    </div>
                </div>

                <div className="d-grid">

                    <button type="submit" className="btn btn-primary">
                        Submit
                    </button>
                </div>
                <p className="forgot-password text-right">
                    Forgot <a href="#">password?</a>
                </p>
            </form>
        )

}