import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import {getToken, refreshToken} from "./authAPI";

const initialState = {
    token: null,
    settings: {},
};
export const getTokenAsync = createAsyncThunk(
    'auth/getToken',
    async (data) => {
        const {items,errors, success} = await getToken(data.email, data.password);
        // The value we return becomes the `fulfilled` action payload
        if (success && errors.length === 0) {
            localStorage.setItem('exp', items.payload.exp)
            localStorage.setItem('email', data.email)
            localStorage.setItem('token', items.token)
            localStorage.setItem('refresh_expires_in', items.refresh_expires_in)
            return {token:items.token, email:data.email,expires_in:items.payload.exp, success:success};
        } else {
            return {token:null, email:null, success:success};
        }
        //else
          //  return null
    }
);
export const refreshTokenAsync = createAsyncThunk(
    'auth/refreshToken',
    async (data) => {
        const {items,errors, success} = await refreshToken(data);
        // The value we return becomes the `fulfilled` action payload
        if (success && errors.length === 0) {
            //console.log(items)
            localStorage.setItem('exp', items.payload.exp)
            localStorage.setItem('email', data.email)
            localStorage.setItem('token', items.token)
            localStorage.setItem('exp', items.exp)

            localStorage.setItem('refresh_expires_in', items.refresh_expires_in)
            return {token:items.token, email:data.email,expires_in:items.payload.exp, success:success};
        } else {
            return {token:null, email:null, success:success};
        }
        //else
        //  return null
    }
);
export const authSlice = createSlice({
    name: 'auth',
    initialState: {
        token: null,
        expires_in:0,
        email:null,
        status:'idle'
    },
    reducers: {

        saveToken: (state,action) => {
            // Redux Toolkit allows us to write "mutating" logic in reducers. It
            // doesn't actually mutate the state because it uses the immer library,
            // which detects changes to a "draft state" and produces a brand new
            // immutable state based off those changes
            //console.log(action.payload)
            state.token = action.payload.token
            state.email = action.payload.email
            state.expires_in = action.payload.expires_in

        },
        saveTokenFromLocalStorage: (state,action) => {
            // Redux Toolkit allows us to write "mutating" logic in reducers. It
            // doesn't actually mutate the state because it uses the immer library,
            // which detects changes to a "draft state" and produces a brand new
            // immutable state based off those changes
            //console.log(action.payload)
            state.token = localStorage.getItem('token')
            state.email = localStorage.getItem('email')
            state.expires_in = localStorage.getItem('expires_in')

        },
        logout: (state,action) => {
            state.token = null


        },

    },
    // The `extraReducers` field lets the slice handle actions defined elsewhere,
    // including actions generated by createAsyncThunk or in other slices.
    extraReducers: (builder) => {
        builder
            .addCase(getTokenAsync.pending, (state) => {
                state.status = 'loading';
            })
            .addCase(getTokenAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                if (action.payload.success) {
                    state.token = action.payload.token
                    state.email = action.payload.email
                    state.expires_in = action.payload.expires_in
                }
            })
    .addCase(refreshTokenAsync.pending, (state) => {
            state.status = 'loading';
        })
            .addCase(refreshTokenAsync.fulfilled, (state, action) => {
                state.status = 'idle';
                if (action.payload.success) {
                    state.token = action.payload.token
                    state.email = action.payload.email
                    state.expires_in = action.payload.expires_in

                }
            });
    },
})
export const { saveToken, saveTokenFromLocalStorage, logout } = authSlice.actions

export default authSlice.reducer