import {graphqlFetch} from "../../hooks/GraphQLHooks";
import {settings} from '../../settings'
const JWT_VERIFY_EXPIRATION = true
const JWT_EXPIRATION_DELTA =  300
//const JWT_REFRESH_EXPIRATION_DELTA = timedelta(days=7)

const GET_AUTH_TOKEN_MUTATION =
    `
    query Login($userid:String!,$password:String!,$deviceid:String!,$os:String!,$clienttype:String!){{ 
    userauth(userinfo: {{userid:$userid, passwd:$passwd, deviceid:$deviceid, os:$os, clienttype:$clienttype}}) {{ 
        userid 
        token 
        success 
    }} 
}} 
`
const REFRESH_TOKEN_MUTATION = 'mutation refresh_token($token: String!) {\n' +
    '  refresh_token(token: $token) {\n' +
    '    token\n' +
    '    payload\n' +
    '    refresh_expires_in\n' +
    '  }\n' +
    '}'
export function refreshToken(token) {
    return graphqlFetch(REFRESH_TOKEN_MUTATION, {token:token},'refresh_token',null,settings.graphqlurl)
}
export function getToken(email, password) {
    return graphqlFetch(GET_AUTH_TOKEN_MUTATION, {email:email, password:password},'token_auth',null,settings.graphqlurl)
}
