import React from 'react'
import { useForm } from "react-hook-form";
import { useSelector, useDispatch } from 'react-redux'
import {useNavigate} from "react-router-dom";
import {yupResolver} from "@hookform/resolvers/yup";
import * as yup from "yup";
import {FirebaseFileUploadInput} from "../apps/common/firebasefileupload";

export  function FormBootstrap({title, afterUrl, dispatchFn, fields, onSubmitFn,yupSchema=yup.object({}), ...props}) {
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const token = useSelector(state=>state.auth.token)
    const { register, handleSubmit, watch, formState: { errors } } = useForm({resolver:yupResolver(yupSchema)});
    const onSubmit = data => {
        if (dispatchFn) dispatch(dispatchFn(data));
        if (onSubmitFn) onSubmitFn(data)
        navigate(afterUrl)
    }

    return (
<div className={"auth-inner"} style={{margin:"0px",width:'50%'}}>
        <form onSubmit={handleSubmit(onSubmit)}>
            <h3 style={{textAlign:"center", marginBottom:"20px", marginTop:"-10px"}}>{title}</h3>

            {fields.map((value,index)=>{

            if (value.type==='input') {
                return (
                    <div className="mb-3">
                    <label>{value.label}</label>
                    <input
                        type={value.validation }
                        className="form-control"
                        placeholder={value.placeholder}
                        {...register(value.name)}
                    />
                </div>)
                        } else if (value.type==='select') {
                return (
                    <div className="mb-3">
                        <label>{value.label}</label>
                    <select className="form-select" aria-label="Default select example">
                    <option selected>{value.instruction}</option>

                    {value.options.map((value1,index)=> {
                       return <option value={value1.option}>{value1.label}</option>
                    })}

                </select>
                    </div>
                        )
            } else if (value.type==='file') {
                return (
                    <div className="mb-3">
                        <label>{value.label}</label>
                        <FirebaseFileUploadInput onUploadFn={value.onUploadFn}/>
                    </div>
                )
            }else {
                return
                    React.cloneElement(value.element, value.props)


            }
            })}
            <div className="d-grid">
                <button type="submit" className="btn btn-primary">
                    Submit
                </button>
            </div>
        </form>
</div>
    )

}