export async function postGraphSqlQuery(url,query,variables,token=null)  {

    let   headers = {
           'Content-Type': 'application/json',
       }
   if (token === null) token = localStorage.getItem('token')

    if (token!=="null"){
        headers.Authorization = 'JWT ' + token
    }
  //console.log(headers)
    const res = await  fetch(url, {
        method: 'POST',
        headers: headers,
        body: JSON.stringify({
            query: query,
            variables:variables

        }),
    })
        return res.json()
        
}
