import React, {useEffect} from 'react';
import logo from './logo.svg';
import { Counter } from './features/counter/Counter';
import {Routes, Route, Link, BrowserRouter, useLocation} from "react-router-dom";
import "./App.css";
import 'bootstrap/dist/css/bootstrap.min.css'
import 'jquery/dist/jquery.min.js'
import 'bootstrap/dist/js/bootstrap.min.js'
import {useDispatch, useSelector} from "react-redux";

import Login from "./apps/auth/components/Login";
import SignUp from "./apps/auth/components/SignUp";
import {AuthTopMenu} from "./apps/auth/components/AuthTopMenu";
import {settings} from "./settings";
import {saveSettings} from "./apps/generic/commonSlice";
import {Sidebar} from "./components/sidebar";
import {saveTokenFromLocalStorage} from "./apps/auth/authSlice";
import {courtapplinks, links} from "./apps/upchakbandi/tests/test-links";

function App() {
  const dispatch = useDispatch()
  useEffect(()=>{
    dispatch(saveTokenFromLocalStorage())
    dispatch(saveSettings(settings))
  },[])


  const authenticated = useSelector(state=>(state.auth.token != null))
  return (
      <div className="App">

        <nav className="navbar navbar-expand-lg navbar-light fixed-top">
          <div className="container">
            <Link className="navbar-brand" to={'/'}>
              e-Chakbandi
            </Link>
            <AuthTopMenu/>
          </div>
        </nav>
        {!authenticated?
        <div className="auth-wrapper">
          <div className="auth-inner">

            <Routes>

              <Route path="/login" element={<Login afterUrl={"/sidebar"}/>} />


              <Route path="/signup" element={<SignUp />} />
            </Routes>
          </div>
        </div>:
        <div className={"container-main"}>
          <Routes>


            <Route path="*" element={<Sidebar  {...courtapplinks}/>}/>



          </Routes>
        </div>}
      </div>
  );
}



export default App;
